module.exports = [[module.id, "/* You can add global styles to this file, and also import other style files */\r\n/*from https://www.w3schools.com/howto/howto_css_blog_layout.asp*/\r\n* {\r\n    box-sizing: border-box;\r\n  }\r\n/* Add a gray background color with some padding */\r\nbody {\r\n    font-family: Arial;\r\n    padding: 20px;\r\n    background: #f1f1f1;\r\n  }\r\n/* Header/Blog Title */\r\n.header {\r\n    padding: 30px;\r\n    font-size: 40px;\r\n    text-align: center;\r\n    background: white;\r\n  }\r\n/* Create two unequal columns that floats next to each other */\r\n/* Left column */\r\n.leftcolumn {   \r\n    float: left;\r\n    width: 75%;\r\n  }\r\n/* Right column */\r\n.rightcolumn {\r\n    float: left;\r\n    width: 25%;\r\n    padding-left: 20px;\r\n  }\r\n/* Fake image */\r\n.fakeimg {\r\n    background-color: #aaa;\r\n    width: 100%;\r\n    padding: 20px;\r\n  }\r\n/* Add a card effect for articles */\r\n.card {\r\n     background-color: white;\r\n     padding: 20px;\r\n     margin-top: 20px;\r\n  }\r\n.edit {\r\n    float:right;\r\n  }\r\n.edit a {\r\n    padding-right: 10px;\r\n  }\r\n/* Clear floats after the columns */\r\n.row:after {\r\n    content: \"\";\r\n    display: table;\r\n    clear: both;\r\n  }\r\n/* Footer */\r\n.footer {\r\n    padding: 20px;\r\n    text-align: center;\r\n    background: #ddd;\r\n    margin-top: 20px;\r\n  }\r\n.alert {\r\n    padding: 20px;\r\n    background-color: #f44336;\r\n    color: white;\r\n    margin-bottom: 15px;\r\n    font-size: .5em;\r\n    /*opacity: 0;\r\n    visibility: hidden;\r\n    transition: visibility 0s 2s, opacity 2s linear; */\r\n     -webkit-animation: fadeout 5s linear 1 normal forwards;\r\n  }\r\n/* Form look and feel */\r\n.box\r\n{\r\n\tposition: relative;\r\n\t/* top: 50%; */\r\n\tleft: 50%;\r\n\t/* transform: translate(-50%, -50%); */\r\n\twidth: 400px;\r\n\tpadding: 40px;\r\n\tbackground: rgba(0,0,0,.7);\r\n\tbox-sizing: border-box;\r\n\tbox-shadow: 0 15px 25px rgba(0,0,0,.5);\r\n\tborder-radius: 10px;\r\n}\r\n.box h2\r\n{\r\n\tmargin: 0 0 30px;\r\n\tpadding: 0;\r\n\tcolor: #fff;\r\n\ttext-align: center;\r\n}\r\n.box .inputBox\r\n{\r\n\tposition: relative;\r\n}\r\n.box .inputBox input\r\n{\r\n\twidth: 100%;\r\n\tpadding: 10px 0;\r\n\tfont-size: 16px;\r\n\tcolor: #fff;\r\n\tletter-spacing: 1px;\r\n\tmargin-bottom: 30px;\r\n\tborder: none;\r\n\tborder-bottom: 1px solid #fff;\r\n\toutline: none;\r\n\tbackground: transparent;\r\n}\r\n.box .inputBox label\r\n{\r\n\tposition: absolute;\r\n\ttop: 0;\r\n\tleft: 0;\r\n\tpadding: 10px 0;\r\n\tfont-size: 16px;\r\n\tcolor: #fff;\r\n\tletter-spacing: 1px;\r\n\tpointer-events: none;\r\n\ttransition: .5s\r\n}\r\n.box .inputBox input:focus ~ label,\r\n.box .inputBox input:valid ~ label\r\n{\r\n\ttop: -18px;\r\n\tleft: 0;\r\n\tcolor: #03a9f4;\r\n\tfont-size: 12px;\r\n}\r\n.box input[type=\"submit\"]\r\n{\r\n\tbackground: transparent;\r\n\tborder:none;\r\n\toutline: none;\r\n\tcolor: #fff;\r\n\tbackground: #03a9f4;\r\n\tpadding: 10px 20px;\r\n\tcursor: pointer;\r\n\tborder-radius: 5px;\r\n}\r\n.flash {\r\n    text-align: center;\r\n    font-size: 1em;\r\n  }\r\n@-webkit-keyframes fadeout{\r\n      from{\r\n         opacity: 1;\r\n      }\r\n      to{\r\n          opacity: 0;\r\n      }\r\n  }\r\n/* Responsive layout - when the screen is less than 800px wide, make the two columns stack on top of each other instead of next to each other */\r\n@media screen and (max-width: 800px) {\r\n    .leftcolumn, .rightcolumn {   \r\n      width: 100%;\r\n      padding: 0;\r\n    }\r\n  }\r\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]